<template>
  <div id="dashApp">
    <b-col>
      <b-row class="dashHeaderDiv">
        <div class="dashHeader">Account Details</div>
      </b-row>
      <b-row>
        <ul class="accountUl">
          <li
            style="padding: 0px 10px"
            class="acctDetailLi"
            v-for="(account, index) in accounts"
            :key="index"
            v-bind="bgStyle(index)"
            @click="selectAcct(index, account)"
          >
            <AccountCardMd
              :class="{
                lightbluedarkblue: index === activeItem,
                lightbluegreen: index === activeItem2,
                pinkpurple: index === activeItem3,
                orangered: index === activeItem4,
                purplepink: index === activeItem5,
                bluegreengreen: index === activeItem6,
                greengrey: index === activeItem7,
                  activeAcct: index === activeAcct,
              }"
              :balance="account.balance"
              :lastFour="account.cardNumber4"
            >
            </AccountCardMd>
          </li>
        </ul>
      
      </b-row>
      <b-row>
        <div class="acctDetailsDiv">
          <div class="acctDetailsHeader"></div>
          <div class="acctDetailBody">
            <div class="acctDetailsSubTitle">Purpose:</div>
            <div class="acctDetailsContent">{{ purpose }}</div>
            <div class="acctDetailsSubTitle">Transactions:</div>
            <div class="acctDetailsContent">{{ transactions }}</div>
            <div class="acctDetailsSubTitle">Debit:</div>
            <div class="acctDetailsContent">+ ${{ debit }}</div>
            <div class="acctDetailsSubTitle">Credit:</div>
            <div class="acctDetailsContent">- ${{ credit }}</div>
          </div>
        </div>
      </b-row>
      <b-row>
        <Table></Table>
      </b-row>
    </b-col>
  </div>
</template>

<script>
import AccountCardMd from "../components/AccountCardMd";
import Table from "../components/Table.vue";
import accounts from "../data/accounts";

export default {
  name: "Dashboard",

  components: {
    AccountCardMd,
    Table,
  },
  data() {
    return {
      accounts: accounts,
      activeAcct: null,
      bgColor: "#ffffff",
      color1: "#fbd786",
      color2: "#ff3b41",
      angle: "80",
      red: null,
      blue: null,
      purple: null,
        activeItem: null,
      activeItem2: null,
      activeItem3: null,
      activeItem4: null,
       activeItem5: null,
      activeItem6: null,
      activeItem7: null,
      purpose: "Business",
      transactions: "2",
      debit: "500",
      credit: "450",
    };
  },
  methods: {
    bgStyle(index, account) {
      console.log(index, account);
     if (index == 0) {
        this.activeItem = index;
      } else if (index == 1) {
        this.activeItem2 = index;
      } else if (index == 2) {
        this.activeItem3 = index;
      }
      else if (index == 3) {
        this.activeItem4 = index;
      } else if (index == 4) {
        this.activeItem5 = index;
      } else if (index == 5) {
        this.activeItem6 = index;
      }
      else if (index == 6) {
        this.activeItem7 = index;
      }
    },
    selectAcct(index, account) {
      console.log(index, account);
      this.activeAcct = index;
      this.purpose = account.purpose;
      this.transactions = account.transactions;
      this.debit = account.debit;
      this.credit = account.credit;
    },
  },
};
</script>

